import React from 'react'
import Link from 'gatsby-link'
import get from 'lodash/get'
import Helmet from 'react-helmet'


// import Lightbox from 'react-images'
import Gallery from "../components/Gallery"

import thumb01 from '../assets/images/thumbs_ML/taza-y-mantelb.jpg'
import thumb02 from '../assets/images/thumbs_ML/taza-y-mantel 2.jpg'

import full01 from '../assets/images/fulls/taza-y-mantel.jpg'
import full02 from '../assets/images/fulls/taza-y-mantel 2.jpg'

const DEFAULT_IMAGES = [
    { id: '1', src: full01, thumbnail: thumb01, caption: 'Photo 1', description: 'Set De Jard√≠n De Infantes Personalizado En Quilmes Cole Kit'},
    { id: '2', src: full02, thumbnail: thumb02, caption: 'Photo 2', description: 'Lorem ipsum dolor sit amet nisl sed nullam feugiat.'}
];

class HomeIndex extends React.Component {

    

    render() {
        //const siteTitle = this.props.data.site.siteMetadata.title
        //const siteDescription = this.props.data.site.siteMetadata.description

        return (
            <div>
                <Helmet>
                        <title>hi</title>
                        <meta name="description" />
                </Helmet>

                <div id="main">

                    <section id="one">
                        <header>
                            <h2>Taza y Mantel</h2>
                        </header>
                        <p>DESCRIPCION:<br /> 
                        * Taza y mantel sublimados con motivos personalizados a eleccion.<br /> 
                        * Consta de: <br /> 
                        - Mantel 35 x 40 cm<br /> 
                        - Taza plastica portafoto.<br /> 
                        Medidas Aproximadas.<br /> 
                        Todo Personalizado a eleccion con imagen y/o foto, nombre y todo ribeteado con cinta al bies<br /> 
                        * Materiales: tropical mecanico.</p>
                       

                        <a mp-mode="dftl" href="https://www.mercadopago.com/mla/checkout/start?pref_id=8068205-506ac82d-d468-4b0d-9b57-666c85608526" name="MP-payButton" className='blue-ar-l-rn-none preview__button'>Pagar</a><br /><br />  

                        <Link to="/">Volver</Link>
                        <br /><br /> 

                    </section>
                
                        <h2></h2>
                    
                        <Gallery images={DEFAULT_IMAGES.map(({ id, src, thumbnail, caption, description }) => ({
                            src,
                            thumbnail
                        }))} />

                        
                    

                </div>

            </div>
        )
    }
}

export default HomeIndex

/*export const pageQuery = graphql
    query PageQuery {
        site {
            siteMetadata {
                title
                description
            }
        }
    }*/



